.Controls__btn {
  padding: 10px;
  border-radius: 5px;
  font-size: 16px;
  text-transform: capitalize;
  border: 1px solid black;
  color: black;
  background-color: white;
  cursor: pointer;
}
.Controls__btn:focus,
.Controls__btn:hover {
  color: white;
  background-color: blue;
}
